<template>
  <v-app>
    <lottie
      :options="defaultOptions"
      v-on:animCreated="handleAnimation"
      v-on:animEnd="handleAnimation"
      class="lottie"
      ref="lottie"
    />
    <v-content>
      <v-container fluid>
        <router-view></router-view>
      </v-container>
    </v-content>
  </v-app> 
</template>

<script>
import lottie from 'vue-lottie'
import * as animationData from './assets/data.json'

export default {
  name: 'app',
  components: {
    lottie
  },
  data () {
    return {
      defaultOptions: {
        animationData: animationData,
        loop: false
      },
      animDirection: 1,
      anim: null,
      isAtBeginning: true
    }
  },
  computed: {
    lottie () {
      return this.$refs.lottie
    }
  },
  methods: {
    handleAnimation: function (anim) {
      this.anim = anim
      this.anim.setSpeed(2)
      this.isAtBeginning = false
      // this.anim.setDirection(-1)
    },
    reverseAnimation: function () {
      this.anim.setDirection(this.animDirection * -1)
      this.anim.play()
    }
  },
  watch: {
    'anim.isPaused' (val, oldval) {
      if (this.anim.currentFrame !== 0) {
        this.reverseAnimation()
        console.log('The counter has changed!')
      }
      console.log(this.anim.currentFrame)
    }
  }
}
</script>

<style>
.lottie {
  position: fixed;
  top: 0;
}

.container {
  max-width: 90% !important;
}
</style>




